<template>
  <div id="tornado"></div>
</template>

<script>
/* eslint-disable */
import * as d3 from "d3";
export default {
  name: "Tornado",
  mounted() {
    this.getTornado();
  },
  methods: {
    getTornado() {
      const data = [
        { type: "稼動率", interactions: -10.1 },
        { type: "稼動率", interactions: 5.1 },
        { type: "拋料率", interactions: -4.9 },
        { type: "拋料率", interactions: 4.9 },
        { type: "直接人力", interactions: -4.3 },
        { type: "直接人力", interactions: 4.3 },
        { type: "良率", interactions: -2.6 },
        { type: "良率", interactions: 2.6 },
      ];
      let chart = this.dornadoChart();
      d3.select("#tornado").datum(data).call(chart);
    },
    dornadoChart() {
      let margin = { top: 20, right: 30, bottom: 40, left: 100 },
        width = 550 - margin.left - margin.right,
        height = 200 - margin.top - margin.bottom;

      let x = d3.scaleLinear().range([0, width]);

      // let y = d3.scale.ordinal().rangeRoundBands([0, height], 0.1);
      let y = d3.scaleBand().rangeRound([0, height]).padding(0.1);

      // let xAxis = d3.svg.axis().scale(x).orient("bottom").ticks(7);
      let xAxis = d3.axisBottom().scale(x).ticks(7);

      // let yAxis = d3.svg.axis().scale(y).orient("left").tickSize(0);
      let yAxis = d3.axisLeft().scale(y).tickSize(0);
      let svg = d3
        .select("body")
        .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

      function chart(selection) {
        selection.each(function (data) {
          x.domain(
            d3.extent(data, function (d) {
              return d.interactions;
            })
          ).nice();
          y.domain(
            data.map(function (d) {
              return d.type;
            })
          );

          let minInteractions = Math.min.apply(
            Math,
            data.map(function (o) {
              return o.interactions;
            })
          );
          yAxis.tickPadding(Math.abs(x(minInteractions) - x(0)) + 10);

          let bar = svg.selectAll(".bar").data(data);

          bar
            .enter()
            .append("rect")
            .attr("class", function (d) {
              return (
                "bar bar--" + (d.interactions < 0 ? "negative" : "positive")
              );
            })
            .attr("x", function (d) {
              return x(Math.min(0, d.interactions));
            })
            .attr("y", function (d) {
              return y(d.type);
            })
            .attr("width", function (d) {
              return Math.abs(x(d.interactions) - x(0));
            })
            .attr("height", y.bandwidth());

          bar
            .enter()
            .append("text")
            .attr("text-anchor", "middle")
            .attr("x", function (d, i) {
              return (
                x(Math.min(0, d.interactions)) +
                Math.abs(x(d.interactions) - x(0)) / 2
              );
            })
            .attr("y", function (d, i) {
              return y(d.type) + y.bandwidth() / 2;
            })
            .attr("dy", ".35em")
            .text(function (d) {
              return `${d.interactions}%`;
            });

          svg
            .append("g")
            .attr("class", "x axis")
            .attr("transform", "translate(0," + height + ")")
            .call(xAxis);

          svg
            .append("g")
            .attr("class", "y axis")
            .attr("transform", "translate(" + x(0) + ",0)")
            .call(yAxis);
        });
      }
      return chart;
    }
  },
};
</script>

